import React from'react';
//import KanjiCard from './KanjiCard'
import KanjiCardFlip from './KanjiCardFlip'
//import To from'./To';
//import From from'./From';


const KanjiCardList = ({kanjis})=>{
	
	const cardComponent = kanjis.map((kanji, i) =>{


		return <KanjiCardFlip 
			id={kanjis[i].id} 
			kanji={kanjis[i].kanji} 
			keyword={kanjis[i].keyword} 
			components={kanjis[i].components} 
			onyomi={kanjis[i].onyomi} 
			kunyomi={kanjis[i].kunyomi}/>
			});
	
	return(
		<div>
		{cardComponent}
		</div>
		)

}

export default KanjiCardList;




