import React, { Component } from "react";
import KanjiCardList from './KanjiCardList';
import {kanjis} from './kanjis';
import SearchBox from './SearchBox';
import Scroll from './Scroll';
//import NavBar from './NavBar';
//import KanjiCardListStudy from './KanjiCardListStudy';


class Browse extends React.Component {
	
	constructor(){
		super()
		this.state = {
			kanjis: kanjis,
			searchfield: ''	
			
		}
	}

onSearchChange = (event) =>{
	this.setState({searchfield: event.target.value});
	}


	render(){

		

		
		const filteredKanjis = this.state.kanjis.filter(kanjis=>{
			return kanjis.keyword.toLowerCase().includes(this.state.searchfield.toLowerCase());
	})

		
		return(
			
				<div>
					<SearchBox searchChange={this.onSearchChange}/>	

					<Scroll >
						<KanjiCardList kanjis={filteredKanjis}/>
					</Scroll>
				</div>
			
		);
	}
}




export default Browse;


