import React from 'react';

const SearchBox = ({searchfield, searchChange}) =>{
	return(
			<div>
				<input style={{margin:10}}className='pa1 br2'
				type='search' 
				placeholder='Search by keyword'
				onChange ={searchChange}
				
				/>
			</div>

		)
}

export default SearchBox;


