import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Browse from "./Browse";
import Study from "./Study";
import About from "./About";
import KanjiCardList from './KanjiCardList';
import {kanjis} from './kanjis';
import SearchBox from './SearchBox';
import Scroll from './Scroll';
//import NavBar from './NavBar';
//import KanjiCardListStudy from './KanjiCardListStudy';


class App2 extends React.Component {
	
	constructor(){
		super()
		this.state = {
			kanjis: kanjis,
			searchfield: ''	
			
		}
	}

onSearchChange = (event) =>{
	this.setState({searchfield: event.target.value});
	}


	render(){

		

		
		const filteredKanjis = this.state.kanjis.filter(kanjis=>{
			return kanjis.keyword.toLowerCase().includes(this.state.searchfield.toLowerCase());
	})

		
		return(
			<HashRouter>
				<div className= 'tc' >
					<div>
						<h1 className="mt2 b mb0 black baskerville i fw1 f1">The Heisig プロジェクト</h1>
		  				<h2 className="mt2 b mb0 black baskerville f4 fw4 ttu tracked">ようこそ!</h2>
		  				<nav className="bt bb black tc mw7 center mt2">
			    			<NavLink to="/" className="f6  link bg-animate black dim hover-bg-lightest-blue-50 dib pa3 ph4-l">Browse</NavLink>
			   				 <NavLink to="/Study" className="f6  link bg-animate black dim hover-bg-lightest-blue-50 dib pa3 ph4-l" >Study</NavLink>
			   				 <NavLink to="/About" className="f6  link bg-animate black dim hover-bg-lightest-blue-50 dib pa3 ph4-l" >About</NavLink>
		  				</nav>
		  				<div className="content">
		  				<Route exact path="/" component={Browse}/>
            			<Route path="/Study" component={Study}/>
            			<Route path="/About" component={About}/>
		  				</div>

					</div>
				</div>
			</HashRouter>
		);
	}
}




export default App2;


