import React from 'react';
import './KanjiCardFlip.css'


const KanjiCardFlip =(props) =>{

	return(
	
		<div className="maincontainer">

      	<div className="thecard">

        <div className="thefront br3 pa2 ma2 bw1">
        	<div>
        		<div className= 'tl  black'>
					{props.id}.
				</div>
				<div>
					<h1>{props.kanji}</h1>
				</div>
				
			</div>

        </div>

        <div className="theback br3 black pa2 ma2 bw1">
    		<div className= 'tl  black'>
				{props.id}.
			</div>
	      	<h3 className='baskerville'>{props.keyword}</h3>
	        <p>{props.components}</p>
	        <p>ON: {props.onyomi}</p>
	        <p>KUN: {props.kunyomi}</p>
	        <div className="more tc">
	        	<p><a className="br4 f7 link dim ba ph3 pv2 mb2 dib white bg-light-red" 
	        	href={`https://app.kanjialive.com/${props.kanji}`}target="_blank">more</a>
	        	</p>
	        </div>
        </div>

      </div>
    </div>
    


	);
}

export default KanjiCardFlip;


