import React, {Component} from "react";
import KanjiCardFlipStudy from "./KanjiCardFlipStudy"
import {kanjis} from './kanjis';


var i= Math.floor(Math.random() * 199) + 1  
 
class Study extends Component {
  
  render() {

	return( 
    	<div>

    		<input style={{width: 60}} className='pa1 ma2 br1' placeholder= 'From:' id='from' type ='number'/>
    		<input style={{width: 60}} className='pa1 ma2 br1' placeholder= 'To:' id='from' type ='number'/>

    		<KanjiCardFlipStudy 

			id={kanjis[i].id} 
			kanji={kanjis[i].kanji} 
			keyword={kanjis[i].keyword} 
			components={kanjis[i].components} 
			onyomi={kanjis[i].onyomi} 
			kunyomi={kanjis[i].kunyomi}/>	
		</div>

			
		);
	}
}
    

 
export default Study;


