import React from 'react';
import './KanjiCardFlipStudy.css'


const KanjiCardFlipStudy =(props) =>{

	return(
	
		<div className="maincontainer2">

      	<div className="thecard2">

        <div className="thefront2 br3 pa2 ma2 bw1">
        	<div>
        	
        		
			<div>
					<h3>{props.keyword}</h3>
				</div>
				
			</div>

        </div>

        <div className="theback2 br3 black pa2 ma2 bw1">
			
	      	<h1>{props.kanji}</h1>
	      	<h3>{props.id}.</h3>
	        <p>{props.components}</p>
	        <p>ON: {props.onyomi}</p>
	        <p>KUN: {props.kunyomi}</p>
	        <div className="more tc">
	        	<p><a className="br4 f7 link dim ba ph3 pv2 mb2 dib white bg-light-red" 
	        	href={`https://app.kanjialive.com/${props.kanji}`}target="_blank">more</a>
	        	</p>
	        	<p><a className="br4 f7 link dim  ph3 pv2  dib white bg-green">story</a>
	        	</p>
	        </div>
        </div>

      </div>
    </div>
    


	);
}

export default KanjiCardFlipStudy;


